import React, {
  FC,
  Children,
  useState,
  useRef,
  useEffect,
  cloneElement,
} from "react";
import clsx from "clsx";

import Highlighter from "./components/Highlighter";
import { TabsProps } from "./types";
import { TabContext } from ".";
import styles from "./style.module.scss";

const Tabs: FC<TabsProps> = ({
  children,
  highlighterClass,
  value,
  onChange,
  className,
}) => {
  const [selectedTabPosition, setSelectedTabPosition] = useState({
    offset: 0,
    width: 0,
  });
  const tabRefs = useRef<Record<TabsProps["value"], HTMLButtonElement>>({});

  useEffect(() => {
    if (tabRefs.current[value]) {
      setSelectedTabPosition({
        offset: tabRefs.current[value].offsetLeft,
        width: tabRefs.current[value].clientWidth,
      });
    }
  }, [value]);

  return (
    <div className={clsx(styles.root, className)}>
      <TabContext.Provider
        value={{
          tabOnChange: onChange,
          value: value,
        }}>
        {Children.map(children, (child, index) => {
          const value = child.props.value ? child.props.value : index;
          return cloneElement(child, {
            ...child.props,
            value,
            ref: (ref: HTMLButtonElement | null) => {
              if (ref && tabRefs.current[value] !== ref) {
                tabRefs.current[value] = ref;
              }
            },
          });
        })}
      </TabContext.Provider>
      <Highlighter
        style={{
          width: selectedTabPosition.width,
          transform: `translateX(${selectedTabPosition.offset}px)`,
        }}
        className={highlighterClass}
      />
    </div>
  );
};

export default Tabs;
